/* стили отдельных блоков */

.preloader-wrapper {
	margin: 3rem auto;
	width: 100%;
}
.preloader-chase {
	width: 5rem;
	height: 5rem;
	position: relative;
	margin: 3rem auto;
	animation: preloader-chase 2.5s infinite linear both;
}

.preloader-chase-dot {
	width: 100%;
	height: 100%;
	position: absolute;
	left: 0;
	top: 0;
	animation: preloader-chase-dot 2s infinite ease-in-out both;
}

.preloader-chase-dot:before {
	content: "";
	display: block;
	width: 25%;
	height: 25%;
	background-color: $accentColor;
	border-radius: 100%;
	animation: preloader-chase-dot-before 2s infinite ease-in-out both;
}

.preloader-chase-dot:nth-child(1) {
	animation-delay: -1.1s;
}
.preloader-chase-dot:nth-child(2) {
	animation-delay: -1s;
}
.preloader-chase-dot:nth-child(3) {
	animation-delay: -0.9s;
}
.preloader-chase-dot:nth-child(4) {
	animation-delay: -0.8s;
}
.preloader-chase-dot:nth-child(5) {
	animation-delay: -0.7s;
}
.preloader-chase-dot:nth-child(6) {
	animation-delay: -0.6s;
}
.preloader-chase-dot:nth-child(1):before {
	animation-delay: -1.1s;
}
.preloader-chase-dot:nth-child(2):before {
	animation-delay: -1s;
}
.preloader-chase-dot:nth-child(3):before {
	animation-delay: -0.9s;
}
.preloader-chase-dot:nth-child(4):before {
	animation-delay: -0.8s;
}
.preloader-chase-dot:nth-child(5):before {
	animation-delay: -0.7s;
}
.preloader-chase-dot:nth-child(6):before {
	animation-delay: -0.6s;
}

@keyframes preloader-chase {
	100% {
		transform: rotate(360deg);
	}
}

@keyframes preloader-chase-dot {
	80%,
	100% {
		transform: rotate(360deg);
	}
}

@keyframes preloader-chase-dot-before {
	50% {
		transform: scale(0.4);
	}
	100%,
	0% {
		transform: scale(1);
	}
}

.error {
	opacity: 1;
	text-align: center;
	font-size: 1.6rem;
}
